<template>
  <div class="timer font-vidaloka h1 d-flex w-100 justify-content-center align-items-center">
    <div class="days d-flex flex-column mx-3">
      <span class="time">{{times.days}}</span>
      <span class="font-playfair unit">Days</span>
    </div>
    <div class="hours d-flex flex-column mx-3">
      <span class="time">{{times.hours}}</span>
      <span class="font-playfair unit">Hours</span>
    </div>
    <div class="minutes d-flex flex-column mx-3">
      <span class="time">{{times.minutes}}</span>
      <span class="font-playfair unit">Minutes</span>
    </div>
    <div class="seconds d-flex flex-column mx-3">
      <span class="time">{{times.seconds}}</span>
      <span class="font-playfair unit">Seconds</span>
    </div>
    <h1 v-if="timeOut">Times Up</h1>
  </div>
</template>

<script>
export default {
  name: 'Countdown',
  props: {
    countDownDate: {
      type: String,
      required: true,
    }
  },
  data() {
    return {
      times: {},
      timeOut: false,
    }
  },

  created() {
    const self = this;

    function initTimer() {
      let remainedTime = self.getRemainedTime(self.countDownDate);
      self.times = self.getNumbers(remainedTime);
      if(remainedTime <= 1000) {
        clearInterval(startTimer);
        self.timeOut = true;
      }
    }
    initTimer();
    let startTimer = setInterval(initTimer, 1000);
  },

  methods: {
    getRemainedTime(time) {
      const endTime = new Date(time).getTime();
      const now = new Date().getTime();

      return endTime - now;
    },

    getNumbers(miliSeconds) {
      const days = Math.floor(miliSeconds / (1000 * 60 * 60 * 24));
      const hours = Math.floor((miliSeconds % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
      const minutes = Math.floor((miliSeconds % (1000 * 60 * 60)) / (1000 * 60));
      const seconds = Math.floor((miliSeconds % (1000 * 60)) / 1000);

      const times = {days, hours, minutes, seconds}
      return times;
    },

    startTimer(startTimer) {
      const remainedTime = this.getRemainedTime(this.countDownDate);
      this.getNumbers();

      if(remainedTime < 0) {
        clearInterval(startTimer);
      }
    }
  },

};
</script>

<style lang="scss" scoped>
.time {
  font-size: 2rem;
}
.unit {
  font-size: 1rem;
  font-style: italic;
}

@media (min-width: 500px) {
  .time {
    font-size: 3.75rem;
  }
  .unit {
    font-size: 1rem;
  }
}
</style>
