<script>
import Countdown from '@/components/Countdown.vue';

export default {
  name: 'Index',
  data() {
    return {
      name: 'Wedding',
      intro: true,
      show: false,
    }
  },
  components: {
    Countdown,
  },
  mounted() {
    setTimeout(() => {
      this.$el.querySelector('.intro').classList.add('fade-in');
    }, 0);
    setTimeout(() => {
      this.$el.querySelector('.intro .title').classList.add('show')
    }, 2000);
    setTimeout(() => {
      this.$el.querySelector('.intro').classList.remove('fade-in');
      this.$el.querySelector('.intro').classList.add('fade-out');
      
      setTimeout(() => {
        this.$el.querySelector('.first').classList.add('fade-in');
        this.show = true;
      }, 1000);
      setTimeout(() => {this.intro = false}, 6000);
    }, 5000);

  },

  methods: {
    start() {
      this.show = true;
      this.$nextTick(() => {
        const distance = this.$el.querySelector('.invitation').offsetTop;
        window.scroll({
          top: distance, 
          left: 0, 
          behavior: 'smooth'
        });
      })
    }
  }
}
</script>

